@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: aliceblue;
  font-family: 'Roboto', sans-serif;
}

.navBar{
  background-color: rgba(144, 238, 144);
  display: flex;
  position: fixed;
  justify-content: space-around;
  width: 100%;
  padding-top:5px;
  border-bottom: 1px solid black;
  z-index: 1;
  height: 80px;
}

.navItem{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

#basketNav{
  position: relative;
  right: -20%;
}

.basket{
  outline: none;
}

.titles{
  flex-basis: 100%;
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
  margin-top: 3%;

}

.homeBtn{
  font-size: 30px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}

.navItem:hover {
  opacity: 0.80;
}
.contactBtn{
  font-size: 30px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}


.basket{
  font-size: 30px;
  padding: 0;
  background: none;
  border: none;
  margin-left: auto;
  cursor: pointer;
}

.h1{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 50px;
}

.contactInfo{
  display: flex;
  position: relative;
  top: -8px;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  background-color: rgba(144, 238, 144);
  border: 1px solid black;
  border-top: none;
  padding: 0px 10px;
  width: 280px;
  
}

.contactItem{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90px;
}



.photos{
  width: 100%;
  height: auto;
  border: 1px solid grey;
}

.favHeader{

  margin: 25px;
}
